
<template>
    <div class="login_m">
        <div class="login_logo"><img src="../../static/img/logo.png" width="196" height="46"></div>
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm login_boder" @keyup.enter.native="submitForm('ruleForm')">
            <el-form-item label="用户名" prop="userphone" class="el-login_name w_per-90">
                <el-input v-model.number="ruleForm.userphone"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password" class="w_per-90">
                <el-input type="password" v-model="ruleForm.password" auto-complete="off"></el-input>
            </el-form-item>
            <div class="el-login_sub">
                <el-button class="login_sub" type="primary" @click="submitForm('ruleForm')" :loading="btnLoading">登录<span v-if="btnLoading">中...</span></el-button>
            </div>
        </el-form>
    </div>
</template>
<script>
export default {
  name: `login`,
  data () {
    const validateUsername = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'))
      } else {
        if (this.ruleForm.password !== '') {
          this.$refs.ruleForm.validateField('password')
        }
        callback()
      }
    }
    const validatePassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    return {
      ruleForm: {
        userphone: 'admin',
        password: '123456'
      },
      rules: {
        userphone: [{ validator: validateUsername, trigger: 'blur' }],
        password: [{ validator: validatePassword, trigger: 'blur' }]
      },
      btnLoading: false
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$router.push('dashboard')
        } else {
          return false
        }
      })
    }
  }
}
</script>
<style>
</style>



