<template>
    <Row>
        <col span="8">
            <Menu accordion>
                <Submenu name="1">
                    <template slot="title">
                        <Icon type="ios-paper" />
                        会员管理
                    </template>
                    <MenuItem name="1-1"><router-link to="/account/accountlist">会员列表</router-link></MenuItem>
                </Submenu>
                <MenuItem style="margin:8px 0" name="2"><Icon type="md-document"/><router-link to="/drugs/drugslist">药品管理</router-link></MenuItem>
                <MenuItem style="margin:8px 0" name="3"><Icon type="md-document"/><router-link to="/order/orderlist">订单管理</router-link></MenuItem>
                <Submenu name="4">
                    <template slot="title">
                        <Icon type="ios-paper"/>
                        消息管理
                    </template>
                    <MenuItem name="4-1"><router-link to="/news/newslist">消息列表</router-link></MenuItem>
                </Submenu>
                 <Submenu name="5">
                    <template slot="title">
                        <Icon type="ios-people" />
                        系统设置
                    </template>
                    <MenuItem name="5-1"><router-link to="/banner/bannerlist">banner设置</router-link></MenuItem>
                    <MenuItem name="5-2"><router-link to="/banner/advertisement">广告设置</router-link></MenuItem>
                    <MenuItem name="5-3"><router-link to="/banner/rule">关于我们</router-link></MenuItem>
                    <MenuItem name="5-4"><router-link to="/banner/share">分享设置</router-link></MenuItem>
                </Submenu>
            </Menu>
        </col>
    </Row>
</template>
<script>
export default {
  data () {
    return {
      theme2: 'light'
    }
  }
}
</script>
<style>
.nav_width {
  height: 100%;
  width: 200px;
  z-index: 2;
}
.ivu-menu-light {
  background: #304156 !important;
  color: white;
}
.ivu-menu-submenu .ivu-menu li a{
  display: block;
  padding-left: 25px;
  color: white;
}
.ivu-menu-submenu {
  padding: 8px 0;
}
.ivu-menu-item a{
    color: white;
}
.ivu-menu-item{
   padding: 8px 0;
}
.ivu-row{
    float: left;
    top: 0px;
    position: fixed;
}
.ivu-menu-opened .ivu-menu .ivu-menu-item-active a{
    color: #2d8cf0;
}
.ivu-menu-light .ivu-menu-item-active a{
    color: #2d8cf0;
}
.ivu-menu-item>i {
    float: left;
}
.ivu-menu-item:hover a{
    color: #2d8cf0;
}
.ivu-icon-md-document{
    padding-right: 7px;
    padding-top: 3px;
}
.ivu-menu-vertical .ivu-menu-opened .ivu-menu .ivu-menu-item:hover a{
    color: #2d8cf0;
}
.ivu-menu-vertical .ivu-menu-opened .ivu-menu .ivu-menu-item:hover{
    background: white;
}
.ivu-row .ivu-menu-light{
    min-height: 1100px;
}
</style>



