<template>
  <div class="content">
    <transition enter-active-class="progress-animated progressFadeIn"></transition>
    <header-component ref="header"></header-component>
    <nav-component :loading='mainLoading' ref="nav"></nav-component>
    <section :class="{ 'active': isCollapse, 'main': true }">
      <transition enter-active-class="router-animated routerFadeInUp">
        <keep-alive :exclude="exclude">
          <router-view/>
        </keep-alive>
      </transition>
    </section>
  </div>
</template>

<script>
import headerComponent from './header'
import navComponent from './nav'
export default {
  name: `mainComponent`,
  components: {
    'header-component': headerComponent,
    'nav-component': navComponent
  },
  data () {
    return {
      exclude: '',
      isCollapse: false,
      mainLoading: false
    }
  },
  created () {
    this.mainLoading = true
  }
}
</script>

<style>
.main {
  position: absolute;
  top: 61px;
  left: 245px;
  right: 0;
  bottom: 0;
  padding: 0px;
  overflow-y: scroll!important;
}

</style>



