// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import axios from 'axios'
import '../static/css/index.css'
import iView from 'iview'
import 'iview/dist/styles/iview.css'
// import 'http://huahong.yongweisoft.cn/static/ueditor/ueditor.config.js'
// import '../static/utf8-php/ueditor.config'
// import '../static/utf8-php/ueditor.all'
// import '../static/utf8-php/lang/zh-cn/zh-cn'
// import '../static/utf8-php/ueditor.parse.min'

Vue.config.productionTip = false
Vue.prototype.$axios = axios

Vue.use(ElementUI)
Vue.use(iView)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



