<template>
  <div>
  <el-card class="box-card">
    <div slot="header" class="clearfix card-header">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>分享</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div v-if='isshow' id="guide-box-ue">
      <el-form :model="addTableData" label-width="100px" class="demo-ruleForm">
            <el-form-item label="标题" style="width:300px;">
              <el-input v-model="addTableData.title" placeholder="请输入标题"></el-input>
            </el-form-item>
            <el-form-item label="描述" style="width:300px;">
              <el-input
                type="textarea"
                autosize
                placeholder="请输入描述"
                v-model="addTableData.dec">
              </el-input>
            </el-form-item>
            <el-form-item label="111" style="width:300px;">
              <el-input v-model="addTableData.point" placeholder="请输入标题"></el-input>
            </el-form-item>
            <el-form-item label="标题" style="width:300px;">
              <el-input v-model="addTableData.title" placeholder="请输入标题"></el-input>
            </el-form-item>
          </el-form-item>
            <el-form-item label="图片">
              <el-upload
                id="el-upload1"
                class="avatar-uploader avatar-uploader_356x178 avatar-uploader_contain"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-change='formImageChange'
                :auto-upload='false'
                :on-success="handleAvatarSuccess">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-form>
      <script id="guide-editor" type="text/plain"></script>
    </div>
    <el-button type="primary" @click="onSubmit" :loading="sureLoading">保存</el-button>
  </el-card>
  </div>
</template>

<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'list',
  data () {
    return {
      sureLoading: false,
      editor: '',
      defaultMsg: '',
      defaultMsgName: '',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      imageUrl: '',
      isshow: true,
      loading: false,
      addTableData: {
        url: '',
        state: 1,
        imageUrl: 'adsfjklasfjklsajfk',
        aid: ''
      }
    }
  },
  activated () {
    this.initUe()
  },
  methods: {
    initUe () {
      let _this = this
      // 初始化UE
      this.editor = window.UE.getEditor('guide-editor', this.config)
      this.editor.addListener('ready', function () {
        // 确保UE加载完成后，放入内容。
        _this.getInfo()
      })
    },
    getInfo () {
      let _this = this
      let d = {
        isUpdate: '2',
        tag: 'share_config'
      }
      this.$axios
        .post(
          domain.testUrl + '/admin/systemsets/system_set',
          qs.stringify(d),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        )
        .then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.info = da.data
            _this.defaultMsg = _this.info['content'] || ''
            _this.editor.setContent(_this.defaultMsg)
          } else {
            this.loading = false
            this.$Notice.warning({ title: '请求失败，请重试' })
          }
        })
        .catch(function () {
          this.$Notice.error({ title: '请求失败，请重试' })
        })
    },
    onSubmit () {
      let _this = this
      let _da = new FormData()
      _this.sureLoading = true
      _da.append('tag', 'UPDSYSTEMCONFIG')
      _da.append('name', _this.info.name)
      _da.append('integral', _this.info.integral)
      _da.append('money', _this.info.money)
      _da.append('limit', _this.info.limit)
      _da.append('logo', _this.info.logo)
      _da.append('guide', _this.editor.getContent())
      _da.append('about', _this.info.about)
      _this.axios.post('/Admin/Interface/Api', _da, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then(function (res) {
        setTimeout(() => {
          _this.sureLoading = false
        }, 600)
        let da = res.data
        if (da.code === 200) {
          _this.$notify({
            title: '提示：',
            message: '提交成功',
            type: 'success'
          })
        } else {
          _this.$notify({
            title: '提示：',
            message: da.data,
            type: 'warning'
          })
        }
      }).catch(err => {
        setTimeout(() => {
          this.sureLoading = false
        }, 600)
        console.error(err)
      })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #guide-box-ue {
    margin-bottom: 15px;
  }
</style>



