<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item  :to="{ path: '../dashboard' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>订单列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative" :model="formInline">
            <div class="from-group" style="width:150px">
              <el-input
                v-model="formInline.order_id"
                placeholder="请输入订单号">
              </el-input>
            </div>
            <div class="from-group form_phone">
              <el-input
                v-model="formInline.phone"
                placeholder="请输入电话号码">
              </el-input>
            </div>
            <div class="from-group" style="width:110px">
                <el-select v-model="formInline.type" placeholder="订单状态">
                    <el-option label="支付" :value="1"></el-option>
                    <el-option label="未支付" :value="2"></el-option>
                </el-select>
            </div>
            <div class="from-group" style="width:150px">
              <el-date-picker
              width="80"
              v-model="formInline.create_date"
              default-time="00:00:00"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="选择日期">
            </el-date-picker>
            </div>
            <div class="from-group">
              <el-button type="primary" @click="getList(true)">查询</el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add('ruleForm')">新增</el-button>
            </div>
        </form>
      </div>
      <el-table
        v-loading="loading"
        :data="tableData"
          border
          style="width: 100%">
          <el-table-column
          width="150"
          label="订单号"
          align='center'>
          <template slot-scope="scope">
            <p class="dtail_number" @click='detail(scope.row.order_id)'>{{scope.row.order_id}}</p>
          </template>
          </el-table-column>
          <el-table-column
          label="电话号码"
          prop="phone"
          align='center'>
          </el-table-column>
          <el-table-column
          align='center'
          prop="total_fee"
          label="订单总价格">
          </el-table-column>
          <el-table-column
          align='center'
          prop="create_date"
          label="创建时间">
          </el-table-column>
          <el-table-column
          align='center'
          label="订单状态">
            <template slot-scope="scope">
                <p v-if="scope.row.status == 0">已删除</p>
                <p v-else-if="scope.row.status == 1">待受理</p>
                <p v-else-if="scope.row.status == 2">待付款</p>
                <p v-else-if="scope.row.status == 3">已付款</p>
                <p v-else-if="scope.row.status == 4">已驳回</p>
            </template>
          </el-table-column>
          <el-table-column
          align='center'
          width="220"
          label="操作">
          <template slot-scope="scope">
            <button type="button" class="el-button el-button--default el-button--small" @click="editor(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
            <button type="button" class="el-button el-button--warning el-button--small" @click="editor(scope.$index,scope.row.id,tableData,'ruleForm')"><span>结算</span></button>
            <button type="button" class="el-button el-button--danger el-button--small" @click="deleted(scope.row.id)"><span>删除</span></button>
          </template>
        </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
        </el-table>
        <!-- 新增 -->
        <el-dialog
        :title="addTableData.aid==='-1'?'新增订单':'修改订单'" v-model="addTableData.id"
        :visible.sync="dialogVisible"
        width="50%">
        <el-form :model="addTableData" label-width="100px" class="demo-ruleForm">
          <el-form-item label="买家电话号码">
            <el-input v-model="addTableData.name" placeholder="请输入电话号码" style="width:160px"></el-input>
          </el-form-item>
          <!-- 买家信息 -->
          <!-- <el-table
          :data="tableDataone"
          style="width: 100%">
          <el-table-column
            label="姓名"
            width="180">
            <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                  <el-tag size="medium">{{ scope.row.name }}</el-tag>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            label="电话号码"
            prop="phone"
            width="180">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
                <Button type="info">确认</Button>
            </template>
          </el-table-column>
        </el-table> -->
        <el-form-item label="卖家电话号码">
          <el-input v-model="addTableData.name" placeholder="请输入电话号码" style="width:160px"></el-input>
        </el-form-item>
        <el-form-item label="商品：">
          <el-input v-model="addTableData.name" placeholder="请输入添加的商品" style="width:160px"></el-input>
        </el-form-item>
        <!-- 商品 -->
        <el-table
          ref="multipleTable"
          :data="tableData3"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange">
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="商品名">
            <template slot-scope="scope">{{ scope.row.date }}</template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="价格">
          </el-table-column>
          <el-table-column
            prop="address"
            label="库存"
            show-overflow-tooltip>
          </el-table-column>
          <el-table-column
            label="数量"
            show-overflow-tooltip>
            <!-- <template slot-scope="scope">
              <button type="button" class="el-button el-button--default el-button--small" @click="ceshi(scope.$index,scope.row.id,tableData,'ruleForm')"><span>编辑</span></button>
            </template> -->
            <template slot-scope="scope">
              <el-input-number v-model="num" @change="handleChange(scope.$index,scope.row.id)" :min="1"></el-input-number>
            </template>
          </el-table-column>
        </el-table>
        <!-- <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div> -->
        </el-form>
        <div slot="footer" class="dialog-footer pop-foot">
          <button type="button"></button>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelForm('ruleForm')">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')" :loading='sureLoading'>确 定</el-button>
        </span>
      </el-dialog>
      <!-- 详情 -->
      <el-dialog :title="'订单详情'" :visible.sync="dialogdetail" width="50%" border-radius="8px">
        <el-table :data="tableDatadetail">
          <el-table-column property="sn" label="批次号"></el-table-column>
          <el-table-column property="company" label="厂家"></el-table-column>
          <el-table-column property="name" label="药名"></el-table-column>
          <el-table-column label="数量">
            <template slot-scope="scope">
                <p>{{scope.row.num}}{{scope.row.specifications}}</p>
            </template>
          </el-table-column>
          <el-table-column property="price" label="价格"></el-table-column>
        </el-table>
        <div class="detail_page">
          <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="pageSizes"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="totaldetail">
          </el-pagination>
        </div>
      </el-dialog>
    </div>
  </main>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      pageSizes: [5, 10, 20, 50],
      pagesize: 10,
      tableData: [],
      tableDatadetail: [],
      total: 0,
      totaldetail: 0,
      loading: true,
      sureLoading: false,
      aid: '',
      // 搜索
      formInline: {
        order_id: '',
        phone: '',
        type: '',
        create_date: ''
      },
      dialogVisible: false,
      dialogdetail: false,
      addTableData: {
        name: '',
        aid: '',
        imgurl: '',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        tag: ''
      },
      tableDataone: [{
        phone: '18428384908',
        name: '王小二',
        address: '上海市普陀区金沙江路 1518 弄'
      }],
      tableData3: [{
        date: '2016-05-03',
        name: '王小虎',
        address: '1000'
      }, {
        date: '2016-05-02',
        name: '王小虎',
        address: '666'
      }, {
        date: '2016-05-04',
        name: '王小虎',
        address: '222'
      }],
      multipleSelection: [],
      num: 1
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    detail (id) {
      let d = {
        tag: 'order',
        op: 'getone',
        condition: {
          'id': id
        },
        data: {
          status: 1
        }
      }
      this.dialogdetail = true
      this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableDatadetail = da.data.goodsinfo.list
          this.totaldetail = da.data.goodsinfo.total
        } else {
          this.loading = false
        }
      }).catch(function () {
        this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    handleSelectionChange (val) {
      this.multipleSelection = val
    },
    handleChange (index, id) {
      // console.log(value);
    },
    add (formName) {
      this.dialogVisible = true
      this.addTableData = {
        name: '',
        aid: '-1',
        imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg',
        stock: '',
        company: '',
        specifications: '',
        price: '',
        sn: '',
        period: '',
        enddate: '',
        sta_goods: 1,
        status: 1,
        remarks: '',
        tag: '',
        num: 1
      }
    },
    editor (index, id, list, formName) {
      this.dialogVisible = true
      this.sureLoading = false
      this.addTableData = Object.assign({}, list[index])
    },
    handleSizeChange (size) {
      this.pagesize = size
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    getList (isSearch) {
      let _this = this
      if (isSearch) {
        this.currentPage = 1
      }
      let d = {
        tag: 'order',
        op: 'getlist',
        condition: {
          'order_id': this.formInline.order_id,
          'phone': this.formInline.phone,
          'type': this.formInline.type,
          'create_date': this.formInline.create_date
        }
      }
      this.$axios.post(domain.testUrl + '/admin/order/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          this.tableData = da.data.list
          this.total = da.data.total
          this.loading = false
        } else {
          // this.$Notice.warning({title: da.message})
          this.loading = false
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    },
    cancelForm (formName) {
      this.dialogVisible = false
      this.$refs[formName].resetFields()
    },
    submitForm (formName) {
      let _this = this
      _this.$refs[formName].validate((valid) => {
        if (valid) {
          this.sureLoading = true
          if (_this.addTableData.aid === '-1') {
            let da = {
              tag: 'product',
              op: 'add',
              data: {
                name: this.addTableData.name,
                stock: this.addTableData.stock,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                remarks: this.addTableData.remarks,
                imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg'
              }
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: '请求失败，请重试'})
            })
          } else {
            let da = {
              tag: 'product',
              op: 'update',
              condition: {
                id: this.addTableData.id
              },
              data: {
                name: this.addTableData.name,
                stock: this.addTableData.stock,
                company: this.addTableData.company,
                specifications: this.addTableData.specifications,
                price: this.addTableData.price,
                sn: this.addTableData.sn,
                period: this.addTableData.period,
                enddate: this.addTableData.enddate,
                sta_goods: this.addTableData.sta_goods,
                status: this.addTableData.status,
                remarks: this.addTableData.remarks,
                imgurl: 'https://abc.yongweisoft.cn./technicianHeadImg/simge/b4cebefeab694f37d4340f0759ec8588.jpg'
              }
            }
            this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(da), {
              headers: {'Content-Type': 'application/x-www-form-urlencoded'}
            }).then(res => {
              let da = res.data
              if (da.err > 0) {
                this.getList()
                this.dialogVisible = false
                this.sureLoading = false
              } else {
                this.sureLoading = false
                _this.$Notice.warning({title: da.message})
              }
            }).catch(function () {
              _this.$Notice.error({title: '请求失败，请重试'})
            })
          }
        } else {
          return false
        }
      })
    },
    // 删除
    deleted (id) {
      let _this = this
      _this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let d = {
          tag: 'product',
          op: 'state',
          condition: {
            id: id
          },
          data: {
            status: '0'
          }
        }
        _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
        }).then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.getList()
            _this.$message({
              type: 'success',
              message: '删除成功!'
            })
          } else {
            _this.$notify({
              title: '提示',
              message: da.data.message,
              type: 'warning'
            })
          }
        }).catch(function () {
          _this.$Notice.error({title: '请求失败，请重试'})
        })
      })
    },
    changeState (id, status) {
      let _this = this
      if (status === 1) {
        status = 2
      } else {
        status = 1
      }
      let d = {
        tag: 'product',
        op: 'state',
        condition: {
          id: id
        },
        data: {
          status: status
        }
      }
      _this.$axios.post(domain.testUrl + '/admin/product/index', qs.stringify(d), {
        headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then(res => {
        let da = res.data
        if (da.err > 0) {
          _this.getList()
          _this.$message({
            type: 'success',
            message: da.message
          })
        } else {
          _this.$notify({
            title: '提示',
            message: da.message,
            type: 'warning'
          })
        }
      }).catch(function () {
        _this.$Notice.error({title: '请求失败，请重试'})
      })
    }
  }
}
</script>



