<template>
  <header>
    <el-menu
      class="el-menu-demo"
      mode="horizontal"
      background-color="#304156"
      @select="handleSelect"
      text-color="black"
      active-text-color="#5cacfe">
      <el-submenu index="2" popper-class="hahaha">
        <template slot="title">我的工作台</template>
      </el-submenu>
    </el-menu>
  </header>
</template>

<script>
export default {
  name: 'headerComponent',
  data () {
    return {
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    toggleMenu () {
      this.showSetting = !this.showSetting
    }
  }
}
</script>

<style>
.el-submenu__title{
    color: #fff !important;
}
  .el-menu--horizontal>.el-submenu {
    float: right!important;
}
  .hahaha .el-menu-item.is-active {
    color: #fff !important;
  }
  .el-menu--horizontal .el-icon-arrow-down {
    color: #fff;
  }
</style>



