<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>会员列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      <form class="from-reset relative">
            <div class="from-group">
              <el-input
              placeholder="请输入用户账号/姓名"
              clearable>
            </el-input>
            </div>
            <div class="from-group">
              <el-button type="primary">查询</el-button>
            </div>
              <div class="from-group">
              <el-button type="primary" @click="add">新增</el-button>
            </div>
        </form>
      </div>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            align='center'
            label="图片">
            <template slot-scope="scope">
              <img :src="scope.row.photo" width="150" height="100">
            </template>
          </el-table-column>
          <el-table-column
            prop="date"
            label="图片"
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="跳转地址"
            width="180">
          </el-table-column>
          <el-table-column
            prop="address"
            label="状态">
          </el-table-column>
          <el-table-column
            align='center'
            label="操作">
            <template slot-scope="scope">
              <button type="button" class="el-button el-button--default el-button--small">
                <span>编辑</span>
                </button>
              <button type="button" class="el-button el-button--danger el-button--small">
                <span>删除</span>
              </button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="pageSizes"
              :page-size="pagesize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
        <el-dialog
        :title="'新增轮播图'"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
        width='500px'
        :visible.sync="addManagerVisible">
        <el-form label-width="100px">
          <el-form-item label="封面图片">
            <el-upload
              id="el-upload1"
              class="avatar-uploader avatar-uploader_356x178 avatar-uploader_contain"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :auto-upload='false'
              accept="image/jpeg, image/png">
              <img class="avatar">
            </el-upload>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addManagerVisible = false">取 消</el-button>
          <el-button type="primary">提 交</el-button>
        </span>
      </el-dialog>
    </div>
  </main>
</template>
<script>
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      pageSizes: [5, 10, 20, 50],
      pagesize: 10,
      selectData: [],
      total: 0,
      tableData: [{
        date: '2016-05-02',
        name: 'www.baidu.com',
        address: '1'
      }, {
        date: '2016-05-04',
        name: 'www.baidu.com',
        address: '2'
      }],
      addManagerVisible: false
    }
  },
  activated () {
    this.getList()
  },
  methods: {
    handleSizeChange (size) {
      this.pagesize = size
      this.getList()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    add () {
      this.addManagerVisible = true
    },
    getList (isSearch) {
      if (isSearch) {
        this.currentPage = 1
      }
      // this.axios
      //   .post(
      //     domain.testUrl + '/user/list?version=v1&page=' + pa + '&limit=10',
      //     qs.stringify({
      //       username: this.formInline.username,
      //       sellerid: this.formInline.sellerid,
      //       state: this.formInline.state,
      //       selleridtostr: 1
      //     }),
      //     {
      //       proxy: {
      //         host: domain.doUrl
      //       },
      //       headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
      //     }
      //   )
      //   .then(res => {
      //     let da = res.data
      //     if (da.status > 0) {
      //       this.tableData = da.data.list
      //       this.total = da.data.total
      //       this.nondata = false
      //       this.loading = false
      //     } else {
      //       this.nondata = true
      //       this.loading = false
      //       this.tableData = []
      //       this.total = 0
      //       this.$notify({
      //         title: '提示：',
      //         message: '暂无数据',
      //         type: 'warning'
      //       })
      //     }
      //   })
    }
  }
}
</script>



