<template>
  <div>
  <el-card class="box-card">
    <div slot="header" class="clearfix card-header">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>关于我们</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div v-if='isshow' id="about-box-ue">
      <script id="about-editor" type="text/plain"></script>
    </div>
    <el-button type="primary" @click="onSubmit">保存</el-button>
  </el-card>
  </div>
</template>
<script>
import qs from 'qs'
import domain from '../../../static/js/url.js'
export default {
  name: 'list',
  data () {
    return {
      editor: '',
      defaultMsg: '',
      defaultMsgName: '',
      config: {
        initialFrameWidth: null,
        initialFrameHeight: 350
      },
      info: {
        name: '',
        integral: '',
        money: '',
        limit: '',
        logo: null,
        guide: '',
        about: ''
      },
      imageUrl: '',
      isshow: true,
      loading: null
    }
  },
  activated () {
    this.initUe()
    //   this.loading = this.$loading({
    //     lock: true,
    //     text: 'Loading',
    //     spinner: 'el-icon-loading',
    //     background: 'rgba(0, 0, 0, 0.7)'
    //   })
  },
  methods: {
    initUe () {
      const _this = this
      // 初始化UE
      this.editor = window.UE.getEditor('about-editor', this.config)
      this.editor.addListener('ready', function () {
        // 确保UE加载完成后，放入内容。
        _this.getInfo()
      })
    },
    getInfo () {
      let _this = this
      let d = {
        isUpdate: '2',
        tag: 'aboutus_config'
      }
      this.$axios
        .post(
          domain.testUrl + '/admin/systemsets/system_set',
          qs.stringify(d),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        )
        .then(res => {
          let da = res.data
          if (da.err > 0) {
            _this.info = da.data
            _this.defaultMsg = _this.info['text'] || ''
            _this.editor.setContent(_this.defaultMsg)
          } else {
            this.loading = false
            this.$Notice.warning({ title: '请求失败，请重试' })
          }
        })
        .catch(function () {
          this.$Notice.error({ title: '请求失败，请重试' })
        })
    },
    onSubmit () {
      let _this = this
      let d = {
        isUpdate: '1',
        tag: 'aboutus_config',
        data: {
          url: '123132',
          title: '123123',
          text: _this.editor.getContent()
        }
      }
      this.$axios
        .post(domain.testUrl + '/admin/systemsets/system_set', qs.stringify(d),
          {
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' }
          }
        )
        .then(res => {
          let da = res.data
          if (da.err > 0) {
            this.$Notice.success({ title: da.message })
          } else {
            this.$Notice.warning({ title: da.message })
          }
        })
        .catch(function () {
          this.$Notice.error({ title: '请求失败，请重试' })
        })
    }
  }
}
</script>
<style>
  .el-forms {
      width: 460px;
  }
  .el-time-spinner.has-seconds .el-time-spinner__wrapper:nth-child(2) {
      margin-left: 0 !important;
  }
  #about-box-ue {
    margin-bottom: 15px;
  }
</style>



