<template>
  <main class="main_content">
    <div class="main-title relative">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>消息列表</el-breadcrumb-item>
      </el-breadcrumb>
    <div class="main-panle">
      </div>
        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="name"
            label="消息内容"
            width="180">
          </el-table-column>
          <el-table-column
            prop="date"
            label="日期"
            width="180">
          </el-table-column>
          <el-table-column
            prop="address"
            label="状态">
          </el-table-column>
          <el-table-column
            align='center'
            label="操作">
            <template slot-scope="scope">
              <button type="button" class="el-button el-button--default el-button--small">
                <span>编辑</span>
                </button>
              <button type="button" class="el-button el-button--danger el-button--small">
                <span>删除</span>
              </button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div>
  </main>
</template>
<script>
export default {
  data () {
    return {
      // 分页数据
      currentPage: 1,
      pageSizes: [5, 10, 20, 50],
      pagesize: 10,
      selectData: [],
      total: 0,
      tableData: [{
        date: '2016-05-02',
        name: '测试测试',
        address: '1'
      }, {
        date: '2016-05-04',
        name: '测试222222',
        address: '2'
      }],
      addManagerVisible: false
    }
  },
  activated () {
    // this.getList()
  },
  methods: {
    handleSizeChange (size) {
      this.pagesize = size
      this.getList()
    },
    handleCurrentChange (currentPage) {
      this.currentPage = currentPage
      this.getList()
    },
    add () {
      this.addManagerVisible = true
    },
    getList (isSearch) {
      if (isSearch) {
        this.currentPage = 1
      }
    }
  }
}
</script>



